.singlePost{
    flex: 9;
}

.singlePostWrapper{
    padding: 20px;
    padding-right: 0;
    display: flex;
    flex-direction: column;
}

.singlePostImg{
    width: 100%;
    height: 300px;
    border-radius: 7px;
    object-fit:cover;
}

.singlePostTitle{
    text-align: center;
    margin: 10px;
    font-family: 'Lora', serif;
    font-size: 28px;
}

.singlePostTitleInput{
    text-align: center;
    margin: 10px;
    font-family: 'Lora', serif;
    font-size: 28px;
    border: none;
    color: gray;
    border-bottom: 1px solid lightgray;
}
.singlePostTitleInput:focus{
    outline: none;
}

.singlePostEdit{
    float: right;
    font-size: 16px;
}

.singlePostIcon{
    margin-left: 10px;
    cursor: pointer;
}
.singlePostIcon:first-child{
   color: teal;
}
.singlePostIcon:last-child{
   color: tomato;
}

.singlePostInfo{
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    font-family: 'Varela Round', sans-serif;
    color: goldenrod;
}

.singlePostDesc{
    color: gray;
    font-size: 18px;
    line-height: 25px;
}

.singlePostDesc::first-letter{
    margin-left: 20px;
    font-size: 30px;
    font-weight: 600;
}

.singlePostDescInput{
    border:none;
    color: gray;
    font-size: 18px;
    line-height: 25px;
    /* border-bottom: 1px solid #000; */
}
.singlePostDescInput:focus{
   outline: none;
}

.singlePostButton{
    width: 100px;
    color:aliceblue;
    border:none;
    background-color: teal;
    padding: 5px;
    border-radius: 5px;
    align-self: flex-end;
    margin-top: 20px;
    cursor: pointer;
}

