.post{
    width: 365px;
    margin:0px 25px 40px 25px;
}

.post > img{
    width: 100%;
    height: 280px;
    object-fit: cover;
    border-radius: 10px;
}

.postInfo{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.postCat {
    font-family: 'Varela Round', sans-serif;
    font-size: 11px;
    color: #be9656;
    line-height: 20px;
    margin: 15px 10px 0px 0px;
    cursor: pointer;
}

.postTitle{
    font-family: "Josephin Sans", sans-serif;
    font-size: 24px;
    font-weight: 700;
    margin-top: 15px;
    cursor: pointer;
}

.postDate {
    font-family: "Lora", serif;
    font-style: italic;
    font-size: 13px;
    color: #998;
    margin-top: 15px;
}

.postDesc{
    font-family: "Varela Round", sans-serif;
    font-size: 14px;
    color:#444;
    line-height: 24px;
    margin-top: 15px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
}